import { useEffect } from "react";

const WebFormOptions = ({ optionSelect }) => {
  const options = optionSelect[0].fieldOptions.forEach(option => (
    <option key={option.id} value={option.value}>
      {option.value}
    </option>
  ));
  useEffect(() => {
      console.log(options);
      console.log(optionSelect[0].fieldOptions);
  })
  return <select>{options}</select>;
};

export default WebFormOptions;
